.contact-us {
  min-height: 600px;
}
.contact-us Button {
  background-color: rgb(255, 142, 43) !important ;
  border-color: rgb(255, 142, 43) !important;
}

.active-contact-item {
  background-color: #ececec94;
}

.icon-container {
  border-radius: 50%;
  height: 35px;
  width: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #808080b8;
  font-size: 1.4rem;
}

.active-contact-item .icon-container {
  background-color: rgb(255, 142, 43);
  color: white;
}

.nav-detail-item {
  border-bottom: 1px solid #80808057;
}

.other-related-banner {
  background-color: #aaa;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 0.9rem;
}

